                {title:"Sites Internet ou Ressources de Tiers",content:"Les Services peuvent contenir des liens vers des sites internet ou des ressources de tiers. Les Cités des Secrets fournit ces liens uniquement pour des raisons de facilité et n'est pas responsable du contenu, des produits ou des services disponibles sur ou à partir de ces sites internet ou ressources, ou des liens affichés sur ces sites. Dans la mesure permise par la loi applicable, vous reconnaissez la responsabilité exclusive et assumez tous les risques découlant de votre utilisation desdits sites Web ou ressources de tiers. Les Cités des Secrets n'est pas responsable de la disponibilité ou de la qualité des services de tiers, en ce compris les réseaux de téléphonie cellulaire, les points d'accès Internet sans fil et autres services. Ces services tiers peuvent affecter votre capacité à utiliser les Services ou à participer à un Événement et vous renoncez par les présentes à toute réclamation, demande, cause d'action, dommage, perte, dépense ou responsabilité pouvant découler de ces services de tiers, en résulter ou s'y rapporter de quelque manière que ce soit, ce à l'encontre de Les Cités des Secrets et toute autre partie concernée par la création ou la prestation des Services."},
                {title:"Exclusion de Garanties",content:"DANS LA MESURE PERMISE PAR LA LOI APPLICABLE, LES SERVICES ET LE CONTENU SONT FOURNIS \"TELS QUELS\", SANS GARANTIE D'AUCUNE SORTE. SANS LIMITER LA PORTÉE DE CE QUI PRÉCÈDE, NOUS DÉCLINONS EXPLICITEMENT TOUTE GARANTIE DE QUALITÉ MARCHANDE, D'ADAPTATION À UN USAGE PARTICULIER, DE JOUISSANCE PAISIBLE OU DE NON-CONTREFAÇON, AINSI QUE TOUTE GARANTIE DÉCOULANT DE RELATIONS D'AFFAIRES PASSEES (‘*COURSE OF DEALING*’) OU DE L'USAGE COMMERCIAL. NOUS NE GARANTISSONS PAS QUE LES SERVICES RÉPONDRONT À VOS BESOINS OU SERONT DISPONIBLES DE FAÇON ININTERROMPUE, SÉCURISÉE OU SANS ERREUR. NOUS NE DONNONS AUCUNE GARANTIE QUANT À LA QUALITÉ, L'EXACTITUDE, L'ACTUALITÉ, LA VÉRACITÉ, L'EXHAUSTIVITÉ OU LA FIABILITÉ DE TOUT CONTENU. VOUS ENDOSSEZ TOUS LES RISQUES LIÉS À VOS COMMUNICATIONS ET INTERACTIONS EN LIGNE OU HORS LIGNE AVEC D'AUTRES UTILISATEURS DES SERVICES ET AVEC D'AUTRES PERSONNES AVEC LESQUELLES VOUS COMMUNIQUEZ OU INTERAGISSEZ EN RAISON DE VOTRE UTILISATION DES SERVICES. VOUS COMPRENEZ QUE LES CITÉS DES SECRETS N'EXAMINE PAS LES ANTÉCÉDENTS DES UTILISATEURS DES SERVICES ET NE S'INFORME PAS À LEUR SUJET. LES CITÉS DES SECRETS NE FAIT AUCUNE DÉCLARATION ET NE DONNE AUCUNE GARANTIE QUANT À LA CONDUITE DES UTILISATEURS DES SERVICES. VOUS ACCEPTEZ DE PRENDRE DES PRÉCAUTIONS RAISONNABLES DANS TOUTES LES COMMUNICATIONS ET INTERACTIONS AVEC D'AUTRES UTILISATEURS DES SERVICES ET AVEC D'AUTRES PERSONNES AVEC LESQUELLES VOUS COMMUNIQUEZ OU INTERAGISSEZ EN RAISON DE VOTRE UTILISATION DES SERVICES, EN PARTICULIER SI VOUS DÉCIDEZ DE RENCONTRER HORS LIGNE OU EN PERSONNE."},
                {title:"Limitation de Responsabilité",content:"DANS LA MESURE PERMISE PAR LA LOI APPLICABLE, NI LES CITÉS DES SECRETS NI AUCUNE AUTRE PARTIE IMPLIQUÉE DANS LA CRÉATION, LA PRODUCTION OU LA LIVRAISON DES SERVICES OU DU CONTENU NE SERONT RESPONSABLES À VOTRE ENCONTRE DE TOUT DOMMAGE INDIRECT, ACCESSOIRE, PARTICULIER, PUNITIF, EXEMPLAIRE OU CONSÉCUTIF, EN CE COMPRIS LA PERTE DE PROFITS, LA PERTE DE DONNÉES OU DE CLIENTÈLE, L'INTERRUPTION DE SERVICE, LES DOMMAGES INFORMATIQUES, LA DÉFAILLANCE DU SYSTÈME OU LE COÛT DES SERVICES SUBSTITUTS, DÉCOULANT DES PRÉSENTES CONDITIONS OU EN RAPPORT AVEC ELLES OU DE LEUR UTILISATION OU INCAPACITÉ À UTILISER CES SERVICES OU CONTENU, OU DE TOUTE COMMUNICATION, INTERACTION OU RÉUNION AVEC D'AUTRES UTILISATEURS DES SERVICES OU PERSONNES AVEC LESQUELLES VOUS COMMUNIQUEZ OU INTERAGISSEZ À LA SUITE DE VOTRE UTILISATION DES SERVICES, QUE CE SOIT SUR LA BASE D'UNE GARANTIE, D'UN CONTRAT, D'UN DÉLIT (EN CE COMPRIS LA NÉGLIGENCE), DE LA RESPONSABILITÉ DU PRODUIT OU DE TOUTE AUTRE THÉORIE JURIDIQUE, ET QUE LES CITÉS DES SECRETS AIT ÉTÉ INFORMÉ OU NON DE LA POSSIBILITÉ DE TELS DOMMAGES, MÊME SI UNE RÉPARATION LIMITÉE ÉNONCÉE DANS LES PRÉSENTES EST RÉPUTÉE AVOIR MANQUÉ À SON BUT PRINCIPAL. CERTAINES JURIDICTIONS N'AUTORISENT PAS L'EXCLUSION OU LA LIMITATION DE RESPONSABILITÉ POUR LES DOMMAGES INDIRECTS OU ACCESSOIRES, DE SORTE QUE LA LIMITATION DE RESPONSABILITÉ SUSMENTIONNÉE S'APPLIQUE DANS TOUTE LA MESURE PERMISE PAR LA LOI DE LA JURIDICTION APPLICABLE. DANS LA MESURE PERMISE PAR LA LOI APPLICABLE, LA RESPONSABILITÉ TOTALE DE LES CITÉS DES SECRETS DÉCOULANT DES PRÉSENTES MODALITÉS, D'UN ÉVÉNEMENT OU DE L'UTILISATION OU DE L'INCAPACITÉ D'UTILISER LES SERVICES OU LE CONTENU NE DÉPASSERA EN AUCUN CAS MILLE DOLLARS ($1.000) OU, SI LA SOCIÉTÉ A PASSÉ UN CONTRAT AVEC LES CITÉS DES SECRETS INTERNATIONAL LIMITED, MILLE LIVRES (£1.000). LES EXCLUSIONS ET LIMITATIONS DE DOMMAGES-INTÉRÊTS ÉNONCÉES CI-DESSUS SONT DES ÉLÉMENTS FONDAMENTAUX DE L'ENTENTE CONCLUE ENTRE LES CITÉS DES SECRETS ET VOUS."},
                {title:"Général",content:"10.1 Intégralité de la Convention\nLes présentes Conditions constituent la seule et entière convention entre Les Cités des Secrets et vous concernant les Services et le Contenu, et les présentes Conditions annulent et remplacent toute entente ou accord antérieur oral ou écrit entre Les Cités des Secrets et vous concernant les Services et le Contenu.\n\n10.2 Divisibilité\nSi l'une des dispositions des présentes Conditions est jugée invalide ou inapplicable, cette disposition sera appliquée dans toute la mesure permise et les autres dispositions des présentes Conditions demeureront pleinement en vigueur. Vous ne pouvez pas céder ni transférer les présentes Conditions, par effet de la loi ou autrement, sans le consentement écrit préalable de Les Cités des Secrets. Toute tentative de votre part de céder ou de transférer les présentes Conditions, sans un tel consentement, sera nulle. Les Cités des Secrets peut librement céder ou transférer les présentes Conditions sans restriction, et le cédant ne demeurera pas conjointement et solidairement responsable. Sous réserve de ce qui précède, les présentes Conditions lieront les parties et seront applicables au bénéfice de celles-ci et de leurs successeurs et ayants droit autorisés.\n\n10.3 Force Majeure\nNi Les Cités des Secrets, ni aucun utilisateur, ni aucune autre partie impliquée dans la création, la production ou la fourniture des Services ou du Contenu ne pourra être tenu responsable d'un dommage, d'une blessure, d'une non-exécution ou d'un retard d'exécution découlant d'un cas de force majeure, de conditions météorologiques, d'un incendie, d'une inondation, d'un acte terroriste ou perpétré par un ennemi étranger, d'une panne d'un satellite ou de réseau, d'un ordre ou d'un règlement du gouvernement, de tout litige commercial ou de toute autre cause indépendante de sa volonté.\n\n10.4 Notification\nToute notification ou autre communication fournie par Les Cités des Secrets en vertu des présentes Conditions, y compris celles concernant des modifications apportées aux présentes Conditions, sera fournie : (a) par e-mail ; ou (b) en l'affichant sur les Services. Pour les notifications fournies par e-mail, la date de réception sera réputée être la date à laquelle la notification est transmise à toute adresse électronique que vous avez fournie.\n\n10.5 Renonciation\nL'inexécution ou la non-application par Les Cités des Secrets d'un droit ou d'une disposition des présentes Conditions ne sera pas considérée comme une renonciation à ce droit ou à cette disposition. La renonciation à un tel droit ou à une telle disposition ne sera applicable que si elle est faite par écrit et signée par un représentant autorisé de Les Cités des Secrets. Sauf stipulations contraires dans les présentes Conditions, l'exercice par une partie de l'un de ses recours en vertu des présentes Conditions sera sans préjudice de ses autres recours en vertu des présentes Conditions ou autre.\n\n10.6 Informations de Contact\nSi vous avez des questions relatives aux présentes Conditions ou aux Services, veuillez contacter Les Cités des Secrets à « créer mail »."},
                {title:"Conditions Spécifiques aux Résidents de l'EEE",content:"Services d'Achats et de Remboursements\nSi vous résidez au sein l'EEE, vous disposez de certains droits de rétractation après avoir effectué des achats en ligne. Cependant, veuillez noter qu'une fois que vous téléchargez de la Monnaie Virtuelle auprès de nous, votre droit de rétractation prend fin. Vous acceptez que (a) l'achat de Monnaie Virtuelle implique le téléchargement immédiat d'un tel Contenu ; et que (b) vous perdez votre droit de rétractation une fois que votre achat est terminé. Si vous résidez dans l'EEE, nous vous fournirons une facture TVA lorsque la loi nous y oblige. Vous acceptez que ces factures puissent être sous forme électronique. Nous nous réservons le droit de contrôler, réglementer, modifier ou supprimer toute Monnaie Virtuelle ou tout Bien Virtuel sans aucune responsabilité à votre égard."},
                {title:"",content:"Ne mourrez pas s'il vous plaît"}
